<template>
    <div class="all" :id="[carousel]">
        <div class="title">{{title}}</div>
        <div class="content">
            <div class="back" :class="carousel" v-if="carousel === 'drawing'" @click="backImage(carousel)"><img  v-bind:src="back" alt=""></div>
            <div class="back" :class="carousel" v-if="carousel === 'web'" @click="backImage(carousel)"><img  v-bind:src="back" alt=""></div>
            <div class="back" :class="carousel" v-if="carousel === 'branding'" @click="backImage(carousel)"><img  v-bind:src="back" alt=""></div>
            <div class="back" :class="carousel" v-if="carousel === 'edits'" @click="backImage(carousel)"><img  v-bind:src="back" alt=""></div>


            <div class="carousel" v-if="carousel === 'drawing'">
                <div v-for="(item, index) of drawing" :key="index" class="section" :class="[carousel +'ex']">
                    <div class="description">
                        <h1>{{item.name}}</h1>
                        <h4>{{item.description}}</h4>
                    </div>
                    <div class="wrapin">
                        <img v-bind:src="item.url" alt="">
                    </div>
                </div>
            </div>
            <div class="carousel" v-if="carousel === 'web'">
                <div v-for="(item, index) of web" :key="index" class="section" :class="[carousel +'ex']">
                    <div class="description">
                        <h1>{{item.name}}</h1>
                        <h4>{{item.description}}</h4>
                    </div>
                    <div class="wrapin">
                        <img v-bind:src="item.url" alt="">
                        
                    </div>
                </div>
            </div>
            <div class="carousel" v-if="carousel === 'branding'">
                <div v-for="(item, index) of branding" :key="index" class="section" :class="[carousel +'ex']">
                    <div class="description">
                        <h1>{{item.name}}</h1>
                        <h4>{{item.description}}</h4>
                    </div>
                    <div class="wrapin">
                        <img v-bind:src="item.url" alt="">
                    </div>
                </div>
            </div>
            <div class="carousel" v-if="carousel === 'edits'">
                <div v-for="(item, index) of edits" :key="index" class="section" :class="[carousel +'ex']">
                    <div class="description">
                        <h1>{{item.name}}</h1>
                        <h4>{{item.description}}</h4>
                    </div>
                    <div class="wrapin">
                        <img v-bind:src="item.url" alt="">
                    </div>
                </div>
            </div>


            <div class="next" :id="carousel" v-if="carousel === 'drawing'" @click="nextImage(carousel)"> <img v-bind:src="next" alt=""></div>
            <div class="next" :id="carousel" v-if="carousel === 'web'"  @click="nextImage(carousel)"> <img v-bind:src="next" alt=""></div>
            <div class="next" :id="carousel" v-if="carousel === 'branding'"  @click="nextImage(carousel)"> <img v-bind:src="next" alt=""></div>
            <div class="next" :id="carousel" v-if="carousel === 'edits'"  @click="nextImage(carousel)"> <img v-bind:src="next" alt=""></div>
        </div>
    </div>
</template>
<script>


export default {
    
  name:'Carousel',
  data(){
    return{
      drawing:[
                {name: 'BACKGROUND COVER #1', description:'April, 2019', url: require('@/assets/images/img-c.png')},
                {name: 'ANIME DRAW', description:'May, 2019', url: require('@/assets/images/img1.png')},
                {name: 'DRAW DIGITALIZATION #1', description:'June, 2019', url: require('@/assets/images/img2.png')},
                {name: 'DRAW DIGITALIZATION #2', description:'Jule, 2019', url: require('@/assets/images/img3.png')},
                {name: 'BACKGROUND COVER #2', description:'Jule, 2019', url: require('@/assets/images/img4.png')},
                {name: 'PHOTO DIGITALIZATION', description:'2019', url: require('@/assets/images/img5.png')}
            ],
            web:[
                {name: 'GROWPAD WEB DESIGN', description:'May, 2019', url: require('@/assets/images/img6.png')},
                {name: '3DIMENSIONAL CONTACT WEB DESIGN', description:'September, 2019', url: require('@/assets/images/img7.png')},
                {name: 'IUJO WEB DESIGN', description:'December, 2019', url: require('@/assets/images/img8.png')}
            ],
            branding:[
                {name: "ORIANA'S BRANDING", description:'May, 2019', url: require('@/assets/images/img9.png')},
                {name: 'PULLCRO LOGO', description:'December, 2019', url: require('@/assets/images/img10.png')},
                {name: 'QUAND LOGO', description:'March, 2019', url: require('@/assets/images/img11.png')},
            ],
            edits:[
                {name: 'QUAND COVER', description:'January, 2020', url: require('@/assets/images/img12.png')},
                {name: 'VIDEO COVER #1', description:'January, 2020', url: require('@/assets/images/img13.png')},
                {name: 'VIDEO COVER #2', description:'December, 2018', url: require('@/assets/images/img14.png')},
                {name: 'VIDEO COVER #3', description:'June, 2018', url: require('@/assets/images/img15.png')},
            ],
            carousel: this.man,
            back: require('@/assets/images/resource17.png'),
            next: require('@/assets/images/resource18.png'),
            y:0
    }
  },
  props:{
      title:String,
      man: String
  },
  methods:{
    backImage(carousel, index){
        var x=0;
        var section = document.getElementsByClassName([carousel]+'ex');
        this.y = this.y- 100;
        if(this.y < 0) this.y = (section.length -1) * 100;
        for(index in section){
            section[index].style=`transform :translate(${-this.y}%);`
        }
        return console.log(this.y);
    },
    nextImage(carousel){
        var index
        var section = document.getElementsByClassName([carousel]+'ex');
        var x = this.y
        this.y = this.y+ 100;
        
        if(this.y > (section.length -1) * 100) this.y = 0;

        for(index in section){
            section[index].style=`transform :translate(${-this.y}%);`
        }       
        return console.log(this.y);
    }
  }
}
</script>
<style>
    .all{
        background: white;
        width: 100%;
        height: 700px;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .title{
        width: fit-content;
        height: fit-content;
        overflow: hidden;
        font-family: Futura, sans-serif;
        font-size: 400%;
        margin-top: 2%;
    }
    .content{
        display: flex;
        flex-direction: row;
        position: relative;
        align-items: center;
        justify-content: center;
        height: 100%;
    }
    .carousel{
        display: grid;
        grid-template-columns: repeat(6,100%);
        height: 90%;
        width: 90%;
    }
    .section{
        position: relative;
        display: flex;
        height: 100%;
        width: 100%;
        align-items: center;
        justify-content: center;
        margin-right: 7%;
        transition: all 0.5s;
    }
    .description{
        position: absolute;
        bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        height: 30%;
        width: 100%;
        background: linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.8));
        z-index: 1000;
    }
    .description h1{
        color: white;
        overflow: hidden;
        font-family: Futura-Bold, sans-serif;
        font-size: 25px;
        height: fit-content;
        text-align: end;
        margin-right: 2%;
    }
    .description h4{
        color: white;
        font-size: 16px;
        overflow: hidden;
        margin-right: 5%;
        font-family: Futura-Book, sans-serif;
    }
    .wrapin{
        width: fit-content;
        position: relative;
        height: 100%;
        overflow: hidden;
    }
    .wrapin img{
        height: 100%;
    }
    .next, .back{
        width: 2.5%;
        height: fit-content;
        cursor: pointer;
        transition: all 0.3s;
        margin-left: 1.3%;
        margin-right: 1.3%;
    }
    .next:hover, .back:hover{
        transform: scale(1.5);
    }
    .next img, .back img{
        width: 100%;
    }
</style>