<template>
    <header class="portrait">
        <div id="l_container">
            <div v-if="bar" id="logo_m" class=" activ" @click="clickLogo"><img src="../assets/images/resource3.png" alt=""></div>
            <div v-if="!bar" id="logo_m" class=" disabl" @click="clickLogo"><img src="../assets/images/resource3.png" alt=""></div>
        </div>
        

        <transition
         name="custom-classes-transition"
         enter-active-class="animated slideInDown"
         leave-active-class="animated slideOutUp">
            <nav class="two" v-if="show" >
                <div class="menub">
                    <ul>
                        <div class="linkst">
                            
                            <li><a href="#drawing">GALLERY</a></li>
                            <li><a href="#about">ABOUT</a></li>
                        </div>
                        <div class="linksi">
                            <li><a href="https://www.facebook.com/quand.mind/" target="blank" class="facebook rrss"></a></li>
                            <li><a href="https://www.instagram.com/quand_mind/" target="blank" class="instagram rrss"></a></li>
                            <li><a href="https://twitter.com/Quand_Mind" target="blank" class="twitter rrss"></a></li>
                        </div>
                    </ul>
                </div>
            </nav>
        </transition>
    </header>
</template>

<script>
export default {
    name:'NavMobile',
    data(){
        return{
            show: false,
            bar: true
        }
    },
    methods:{
        clickLogo(){
            var nav = document.getElementById('l_container')
            
            setTimeout(() =>{
                if(this.bar === false) nav.style="background: white;"
                else nav.style="background: none;"
            },200)
            this.show = !this.show
            this.bar = !this.bar
        }
    }
}
</script>
<style>
    .portrait{
        display: none;
    }
    #logo_m{
        background: white;
        cursor: pointer;
        display: flex;
        width: 60px;
        height: 60px;
        align-items: center;
        justify-content: center;
        z-index: 1000;
        transition: all .5s;
    }
    .two{
        width: 100%;
        background: white;
        overflow: hidden;
    }
    #l_container{
        width: 100%;
        height: 60px;
        transition: all 0.5s;
        z-index: 1000;
    }
    #logo_m img{
        width: 60%;
        height: auto;
    }
    .menub{
        height: 100%; 
    }
</style>