<template>
  <transition name="fade">
      <div v-if="show" class="preloader">
        <div class="load"></div>
        <div class="points">
          <div></div>
          <div></div>
          <div></div>
        </div>
        
      </div>
      
  </transition>
    
</template>
<script>
export default {
    name:'Load',
    data(){
      return{
        show:true
      }
    },
    mounted(){
      this.showToggle()
    },
    methods:{
      showToggle(){
        setTimeout(() =>{
          
          this.show= false
          var htmlElement = document.querySelector("html");
          htmlElement.style ="overflow:auto;";
        },3000)
      }
    }
}
</script>
<style>
.preloader{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 100%;
    height: 100%;
    background:linear-gradient(to right,#0B2E4D,#204668);
    z-index: 2000;
}
.preloader .load{
  background-image:url(../assets/images/resource3.png); 
  height: 12.5rem;
  width: 12.5rem;
  background-size: contain;
  background-repeat: no-repeat;
}
.points div{
  height: 1.5rem;
  width: 1.5rem;
  background: white;
  margin: 0px 10px;
  border-radius: 100%; 
  transform:scale(0); 
}
.points{
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}
.points :nth-child(1){
  animation-delay: 0s;
}
.points :nth-child(2){
  animation-delay: 0.2s;
}
.points :nth-child(3){
  animation-delay: 0.4s;
}



.fade-enter-active, .fade-leave-active {
  transition: opacity 1s;
}
.points div{
  animation-name: scaleIn;
  animation-duration: 2s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;

}


@keyframes scaleOut{
  0%{
    transform: scale(1);
  }
  50%{
    transform:scale(0);
  }
  100%{
    transform:scale(1);
  }
}

@keyframes scaleIn{
  0%{
    transform: scale(0);
  }
  25%{
    transform: scale(1)
  }
  50%{
    transform: scale(0)
  }
  75%{
    transform: scale(1)
  }
  100%{
    transform:scale(0);
  }
}


</style>